  display: block;
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  border-radius: 50%;
}

.staff__button {
  top: 13%;
  border: none;
  display: block;
  background-color: transparent;
  height: 87%;
  margin: 0;
}
.staff__button::before {
  content: "";
  display: block;
  width: 23px;
  height: 23px;
  border: 1px solid #afafaf;
  border-top: none;
  border-right: none;
  transform: rotate(45deg);
}
.staff__button--prev {
  left: -4%;
}
.staff__button--next {
  right: -4%;
}
.staff__button--next::before {
  transform: rotate(-135deg);
}

